import composeRefs from '@seznam/compose-react-refs';
import { AnimatePresence, motion } from 'framer-motion';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { useFocus, useFocusRing, useFocusVisible, useFocusWithin } from 'react-aria';
import { transitions } from '../../../hooks/transitions';
import { clsx } from '../../../libs/clsx';
import { forwardRefWrapper } from '../../../libs/forward-ref-wrapper';
import { mergeProps } from '../../../libs/merge-props';
import { IntrinsicElementProps } from '../../../types/utility';
import { Icon, MonochromeIcons } from '../../icons';
import { Flex, Outset } from '../../layout';
import { useTheme } from '../../theme-provider';

import styles from './text-field.less';

interface TextFieldProps extends Omit<IntrinsicElementProps<'input'>, 'size' | 'prefix'> {
  disabled?: boolean;
  label?: string;
  errorMessage?: string;
  validateOnBlur?: boolean;
  size?: 'sm' | 'md' | 'lg';
  prefix?: React.ReactNode;
  suffix?: React.ReactNode;
  addonBefore?: React.ReactNode;
  addonAfter?: React.ReactNode;
}

const TextFieldRender = /* @__PURE__ */ forwardRefWrapper<HTMLInputElement, TextFieldProps>(
  'TextField',

  { size: 'md' },

  (props, externalRef) => {
    const {
      id,
      required,
      disabled,
      label,
      placeholder,
      errorMessage,
      validateOnBlur,
      size,
      prefix,
      suffix,
      addonBefore,
      addonAfter,
      ...otherProps
    } = props;

    const [hasError, setHasError] = useState(!!errorMessage);

    useEffect(() => {
      setHasError(!!errorMessage);
    }, [errorMessage]);

    const onBlur = useCallback<React.FocusEventHandler<HTMLInputElement>>(
      (e) => {
        if (validateOnBlur) {
          const valid = e.target.checkValidity();
          setHasError(!valid || !!errorMessage);
        }
      },
      [errorMessage],
    );

    // Focus
    const [isFocused, onFocusChange] = useState(false);
    const [isFocusedWithinAddonBefore, onFocusWithinAddonBeforeChange] = useState(false);
    const [isFocusedWithinAddonAfter, onFocusWithinAddonAfterChange] = useState(false);
    const { focusProps } = useFocus({ isDisabled: disabled, onBlur, onFocusChange });
    const { focusWithinProps: focusWithinAddonBeforeProps } = useFocusWithin({
      isDisabled: disabled,
      onFocusWithinChange: onFocusWithinAddonBeforeChange,
    });
    const { focusWithinProps: focusWithinAddonAfterProps } = useFocusWithin({
      isDisabled: disabled,
      onFocusWithinChange: onFocusWithinAddonAfterChange,
    });
    const { isFocusVisible } = useFocusVisible({ isTextInput: true });

    const inputRef = useRef<HTMLInputElement | null>(null);
    const focusOnInput = useCallback(() => {
      if (inputRef.current) inputRef.current.focus();
    }, [inputRef]);

    const getScaleProps = transitions.useScale();
    const theme = useTheme();

    return (
      <Flex.Column>
        {label && (
          <Outset bottom={8}>
            <label htmlFor={id} className={clsx([styles.TextFieldLabel, styles[`size_${size}`]])}>
              <Flex.Row alignItems="center">
                <Outset right={3} trim="horizontal">
                  <span>{label}</span>
                  {required && <Icon size={10} color={theme.hex.error.darker} type={MonochromeIcons.AsteriskBold} />}
                </Outset>
              </Flex.Row>
            </label>
          </Outset>
        )}

        <Flex.Row
          alignItems="center"
          onClick={focusOnInput}
          className={clsx([
            styles.TextField,
            disabled && styles.disabled,
            isFocused && styles.focus,
            (isFocusedWithinAddonBefore || isFocusedWithinAddonAfter) && isFocusVisible && styles.focusWithin,
            isFocusedWithinAddonBefore && isFocusVisible && styles.focusWithinAddonBefore,
            isFocusedWithinAddonAfter && isFocusVisible && styles.focusWithinAddonAfter,
            hasError && styles.hasError,
          ])}
        >
          {addonBefore && (
            <Flex.Item
              {...focusWithinAddonBeforeProps}
              shrink={0}
              alignSelf="stretch"
              className={clsx(styles.addon, styles.before)}
            >
              {addonBefore}
            </Flex.Item>
          )}

          <Flex.Row grow={1} alignItems="center" className={clsx(styles.paddingContainer, styles[`size_${size}`])}>
            <AnimatePresence initial={false} mode="wait">
              {prefix && (
                <motion.div className={styles.prefix} {...getScaleProps(0.7)} key={0}>
                  {prefix}
                </motion.div>
              )}
            </AnimatePresence>

            <input
              {...mergeProps(
                {
                  id,
                  tabIndex: disabled ? -1 : 0,
                  disabled,
                  'aria-label': !label && !!placeholder ? placeholder : undefined,
                  placeholder,
                },
                focusProps,
                otherProps,
              )}
              ref={composeRefs(inputRef, externalRef)}
            />

            <AnimatePresence initial={false} mode="wait">
              {suffix && !hasError && (
                <motion.div className={styles.suffix} {...getScaleProps(0.7)} key={0}>
                  {suffix}
                </motion.div>
              )}
            </AnimatePresence>
          </Flex.Row>

          {addonAfter && (
            <Flex.Item
              {...focusWithinAddonAfterProps}
              shrink={0}
              alignSelf="stretch"
              className={clsx(styles.addon, styles.after)}
            >
              {addonAfter}
            </Flex.Item>
          )}
        </Flex.Row>

        {errorMessage && (
          <Outset top={8} left={0}>
            <span className={clsx([styles.TextFieldErrorMessage, styles[`size_${size}`]])}>{errorMessage}</span>
          </Outset>
        )}
      </Flex.Column>
    );
  },
);

const TextFieldAddonButton = /* @__PURE__ */ forwardRefWrapper<HTMLButtonElement, IntrinsicElementProps<'button'>>(
  'TextField.AddonButton',

  (props, externalRef) => {
    const { disabled, autoFocus, children, ...otherProps } = props;
    const { isFocusVisible, focusProps } = useFocusRing({ autoFocus });

    return (
      <Flex.Row vertical="center" horizontal="center" className={styles.AddonButton}>
        {/* eslint-disable-next-line react/button-has-type */}
        <button
          {...mergeProps(
            {
              className: clsx(disabled && styles.disabled, isFocusVisible && styles.focus),
              disabled,
              autoFocus,
            },
            focusProps,
            otherProps,
          )}
          ref={externalRef}
        >
          {children}
        </button>
      </Flex.Row>
    );
  },
);

export const TextField = /* @__PURE__ */ Object.assign(TextFieldRender, {
  AddonButton: TextFieldAddonButton,
});
